.currentWeather {
    width: 100%;
    // height: 480px;
    margin-top: 3rem;

    hr {
        border: none;
        background-color: #dbdbdb;
        width: 100%;
        height: 1px;
        border-top: 0.5px solid #ffffff;
        margin-top: 12%;
    }

    .currentWeather__visual {
        // background: url(../public/img/sunny.png) no-repeat;
        // background-position: left;
        // background-size: contain;
        width: 100%;
        height: 260px;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            width: 90%;
            height: 90%;
            object-fit: contain;
        }
    }

    .currentWeather__temp {
        width: 100%;
        margin-top: 4%;

        p {
            margin: 4% 0 0 0;
            font-size: 78px;

            span {
                font-size: 48px;
                position: relative;
                top: -22px;
            }
        }
    }

    .currentWeather__date {
        width: 100%;
        height: 24px;
        margin-top: 2%;

        p {
            font-size: 18px;
            font-weight: 500;

            span {
                opacity: 0.5;
            }
        }
    }

    .currentWeather__param {
        margin-top: 8%;
        width: 100%;

        .param__wwcode {
            display: flex;
            width: 100%;
            align-items: center;
            margin-top: 2rem;

            span {
                font-size: 16px;
                margin-left: 0.6rem;
            }

            .param__iconWrapper {
                display: inline-block;
                width: 16px;
                height: 16px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }

        .param__rain {
            display: flex;
            width: 100%;
            align-items: center;
            margin-top: 1rem;

            span {
                font-size: 16px;
                margin-left: 0.6rem;
            }

            .param__iconWrapper {
                display: inline-block;
                width: 16px;
                height: 16px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
    }
}

.currentWeather__load {
    width: 100%;
    // height: 480px;
    margin-top: 3rem;

    hr {
        border: none;
        background-color: #dbdbdb;
        width: 100%;
        height: 1px;
        border-top: 0.5px solid #ffffff;
        margin-top: 12%;
    }

    .currentWeather__visual {
        width: 100%;
        height: 260px;
        display: flex;
        background-color: #dbdbdb;
        border-radius: 12px;
        transition: all 1s ease-out;
        animation: blinking 2s linear infinite;

        img {
            display: none;
        }
    }

    .currentWeather__temp {
        width: 100%;
        height: 104px;
        margin-top: 4%;
        background-color: #dbdbdb;
        border-radius: 12px;
        transition: all 1s ease-out;
        animation: blinking 2s linear infinite;

        p {
            display: none;

            span {
                display: none;
            }
        }
    }

    .currentWeather__date {
        width: 100%;
        height: 24px;
        margin-top: 2%;
        background-color: #dbdbdb;
        border-radius: 12px;
        transition: all 1s ease-out;
        animation: blinking 2s linear infinite;

        p {
            display: none;

            span {
                display: none;
            }
        }
    }

    .currentWeather__param {
        margin-top: 8%;
        width: 100%;
        height: 58px;
        background-color: #dbdbdb;
        border-radius: 12px;
        transition: all 1s ease-out;
        animation: blinking 2s linear infinite;

        .param__wwcode {
            display: none;
        }

        .param__rain {
            display: none;
        }
    }
}

@media screen and (max-width:430px) {
    .current-weather {
        width: 100%;
        // height: 480px;
        margin-top: 3rem;

        hr {
            border: none;
            background-color: #dbdbdb;
            width: 100%;
            height: 1px;
            border-top: 0.5px solid #ffffff;
            margin-top: 12%;
        }

        .current-weather__visual {
            // background: url(../public/img/sunny.png) no-repeat;
            // background-position: left;
            // background-size: contain;
            width: 100%;
            height: 260px;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 90%;
                height: 90%;
                object-fit: contain;
            }
        }

        .current-weather__temp {
            p {
                margin: 4% 0 0 0;
                font-size: 78px;

                span {
                    font-size: 48px;
                    position: relative;
                    top: -22px;
                }
            }
        }

        .current-weather__date {
            p {
                font-size: 18px;
                font-weight: 500;

                span {
                    opacity: 0.5;
                }
            }
        }

        .current-weather__param {
            margin-top: 8%;
            width: 100%;

            .param__wwcode {
                display: flex;
                width: 100%;
                align-items: center;
                margin-top: 2rem;

                span {
                    font-size: 16px;
                    margin-left: 0.6rem;
                }

                .param__icon-wrapper {
                    display: inline-block;
                    width: 16px;
                    height: 16px;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: contain;
                    }
                }
            }
        }
    }
}

@keyframes blinking {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}